.footer{
    width: 100%;
    padding: 6rem 0;
    background-color: rgba(19,19,19,0.8);
}

.footer-container{
    max-width: 1140px;
    margin:  auto;

    display: grid;
    grid-template-columns:repeat(2,1fr);
    grid: gap 40px;
}

.left{
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
}
.location{
    display: flex;
    margin-bottom: 0.8rem;
}
.location p{
    line-height: 30px;
}
.phone{
    padding-bottom: 10px;
}
.right{
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 6rem 0 0 20rem;
}
.social{
    margin-top: 1rem;
    display: flex;
}
@media  screen and(max-width:640px){
    .footer-container{
        grid-template-columns: 1fr;
    }
    
}